export default {
	data() {
		return {
			type:'password',
			isuser:false,  // 用户名是否占用
			isemail:false, // 邮箱是否被占用
			look:true,    // 是否显示密码
			invalid:false, // 邮箱是否符合
			employ:false,  // 是否被占用
			lookurl:'../../static/images/unlook.png',
			email:'',      // 邮箱
		};
	},
	methods:{
		//密码显示隐藏
		looks:function(){
			console.log('1111')
			if(this.look){
				this.type = 'password';
				this.lookurl = '../../static/images/unlook.png'
			}else{
				this.type = "text";
				this.lookurl = '../../static/images/look.png'
			}
			this.look = !this.look;
		},
		
		//判断是否是邮箱格式
		isEmail:function(e){
			this.email = e.detail.value;
			let reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
			if(this.email.length > 0){
				if(reg.test(this.email)){
					this.invalid = false;
				}else{
					this.invalid = true;
				}
			}
		}
	}
};
